.map__wrapper {
  max-width: 320px;
  margin: 0 auto;
  background-color: @color-white;
}

@media screen and (min-width: @tablet-width) {
  .map__wrapper {
    max-width: @tablet-width;
    margin: 0 auto;
  }

  .map__google-map {
    width: @tablet-width;
    height: 350px;
  }
}

@media screen and (min-width: @desktop-width) {
  .map__wrapper {
    max-width: @desktop-width;
    margin: 0 auto;
  }

  .map__google-map {
    width: @desktop-width;
    height: 350px;
  }
}
